.slider {
  position: relative;
}

.slider__inner {
  position: relative;
  width: 290px;
  margin: 0 auto;
}

.slider__list {
  @include list-reset;

  margin: 0 auto 10px;
}

.slider__controls {
  @include list-reset;

  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  padding: 10px 0;
  max-width: 360px;

  li {
    width: 20%;
  }
}

.slider__bullet {
  position: relative;
  display: block;
  width: 100%;
  height: 3px;
  border-radius: 5px;
  background-color: rgba($color: $white, $alpha: 0.3);
  transition: background-color 0.3s;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: -5px;
    width: 100%;
    height: 13px;
  }
}

.slider__item {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  margin: 0 auto 15px;
  width: 290px;

  a {
    position: relative;
    display: block;
  }

  img {
    width: 100%;
    height: auto;
  }

  p {
    position: absolute;
    bottom: 15px;
    left: calc(50% - 125px);
    margin: 0;
    max-width: 230px;
    font-weight: normal;
    font-size: 16px;
    line-height: 27px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: rgba($color: $white, $alpha: 0.9);
    transition: all 0.5s ease;
  }

  .button {
    position: absolute;
    bottom: 20px;
    left: 20px;
    display: flex;
    width: 200px;
    background-color: $white;
    color: $black;
    opacity: 0;
    cursor: pointer;
    transition: all 0.5s ease;
  }
}

@media (min-width: $tablet) {
  .slider__inner {
    width: 658px;
  }

  .slider__item {
    width: 330px;

    &:hover {
      .button {
        opacity: 1;
      }

      p {
        bottom: 75px;
      }
    }
  }

  .slider__controls li {
    width: 45%;
  }
}

@media (min-width: $desktop) {
  .slider__inner {
    width: 100%;
  }

  .slider__list {
    display: flex;
  }

  .slider__item {
    margin-top: 15px;
    width: 290px;
    box-shadow: transparent, transparent;
    transition: all 0.3s ease;

    &:hover {
      box-shadow: 0 0 0 10px $bg-main, 0 0 0 11px $white-faded;
    }
  }
}
